.site-footer {
  background-color: #2c3e50;
  color: #ecf0f1;
  padding: 1.5rem 0;
  margin-top: 2rem;
  width: 100%;
  box-sizing: border-box;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 2rem;
  box-sizing: border-box;
}

.footer-info h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1.2rem;
  font-weight: 500;
}

.footer-info p {
  margin: 0;
  font-size: 0.9rem;
  color: #bdc3c7;
}

.footer-contacts {
  display: flex;
  gap: 1.2rem;
}

.footer-contact-item {
  color: #ecf0f1;
  font-size: 1.4rem;
  transition: color 0.2s ease, transform 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-contact-item:hover {
  color: #3498db;
  transform: translateY(-3px);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .footer-info {
    margin-bottom: 1rem;
  }
} 